/* CSS that will be loaded on the frontend of single posts (except Pages) */

@import 'variables';

#top{
    .single-hero-section,
    .single-content-section{
        .container{
            max-width: calc( #{$container-single} + 100px );
        }
    }
    .single-hero-section{
        background-size: cover!important;
        background-attachment: fixed!important;
        h1, .h1{
            @media only screen and (max-width: 767px){
                line-height: 1.2;
            }
        }
        .content{
            padding-top: 69px;
            padding-bottom: 69px;
            .single-meta{
                font-size: 12px;
                margin-bottom: 30px;
                .single-date{
                    font-weight: $bold;
                    letter-spacing: calc(( 12px / 1000 ) * 100);
                    color: #304D7C;
                    margin-right: 12px;
                    font-family: $heading-font-family;
                }
                .single-term{
                    text-align: center;
                    border: 1px solid $orange;
                    border-radius: 21px;
                    padding: 1px 10px;
                    color: $orange;
                    font-family: $extra-font-family;
                }
            }
            .single-post-title{
                font-weight: $light;
                color: $pink4;
            }
        }
        &.has-thumb{
            .content{
                padding-bottom: 202px;
                @media only screen and (min-width: 768px){
                    padding-bottom: 282px;
                }
            }
        }
        &.single-video-hero{
            .single-term{
                border-color: $blue3!important;
                color: $blue3!important;
            }
        }
        &.single-download-hero{
            .single-term{
                border-color: $green!important;
                color: $green!important;
            }
        }
        &.single-press-release-hero{
            .single-term{
                border-color: $pink!important;
                color: $pink!important;
            }
        }
    }
    .single-content-section{
        line-height: 1.8;
        @media only screen and (min-width: 990px){
            font-size: 20px;
        }
        h1, h2, h3, h4, h5, h6,
	    .h1, .h2, .h3, .h4, .h5, .h6{
            margin-top: 1.5em;
        }
        h1, .h1{
            
        }
        h2, .h2{
            
        }
        h3, .h3{
        }
        h4, .h4{
            font-size: 20px;
        }
        h5, .h5{
        }
        h6, .h6{
        }
        blockquote{
        }
        ul{
            li{
                padding-left: 34px!important;
                font-size: 16px;
                &::before{
                    width: 4px!important;
                    height: 4px!important;
                    top: 1.1em!important;
                }
            }
        }
        p{
            font-size: 15px;
            line-height: 1.6;
            @media only screen and (min-width: 768px){
                font-size: 16px;
            }
        }
        .single-thumb-wrapper{
            .single-thumb{
                max-width:  770px!important;
                margin: 0 auto;
                position: relative;
                padding-bottom: 57.01%;
                img{
                    border-radius: 14px;
                    width: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 100%;
                    object-fit: cover;
                    object-position: center center;
                }
            }
        }
        .single-footer-buttons{
            display: flex;
            justify-content: space-between;
            a{
                transition: color ease 0.25s;
                &:hover{
                    text-decoration: none;
                }
            }
            .single-back-button{
                font-size: 16px;
                display: flex;
                align-items: center;
                a{
                    position: relative;
                    padding-left: 35px;
                    line-height: 1.2;
                    &::before{
                        content: '\e807';
                        font-family: 'rxpharmacy-icons';
                        position: absolute;
                        left: 0;
                        top: 50%;
                        transform: translateY(-50%) rotate(180deg);
                    }
                }
            }
            .av-share-box{
                ul{
                    flex-direction: row;
                    li{
                        padding-left: 0!important;
                        &:not(:last-child){
                            margin-right: 25px;
                        }
                        a{
                            font-size: 26px;
                            color: $pink3;
                        }
                    }
                }
            }
        }
        .post-authors{
            margin-top: 50px!important;
            position: relative;
            padding-top: 76px;
            &::before{
                content: "";
                width: 100%;
                height: 1px;
                background-color: $darkblue2;
                position: absolute;
                top: 0;
                left: 0;
            }
            .single-post-author{
                display: flex;
                align-items: flex-start;
                @media only screen and (max-width: 767px){
                    flex-direction: column;
                    align-items: center;
                    text-align: center;
                }
                &:not(:last-child){
                    margin-bottom: 20px;
                }
                .author-thumb{
                    position: relative;
                    width: 138px;
                    min-height: 138px;
                    @media only screen and (min-width: 768px){
                        margin-right: 62px;
                    }
                    &::after{
                        content: "";
                        width: 100%;
                        height: 100%;
                        border-radius: 100%;
                        border: 7px solid $blue3;
                        position: absolute;
                        top: 0;
                        left: 0;
                        box-sizing: border-box;
                    }
                    img{
                        width: 100%;
                        border-radius: 100%;
                        display: block;
                    }
                }
                .author-meta{
                    @media only screen and (min-width: 768px){
                        width: calc(100% - 200px);
                    }
                    h4,h6{
                        margin-top: 0;
                    }
                    h6{
                        @media only screen and (max-width: 767px){
                            margin-top: 20px;
                        }
                    }
                    .single-author-name{
                        margin-top: 6px;
                        letter-spacing: 0;
                        @media only screen and (max-width: 767px){
                            margin-top: 10px;
                        }
                    }
                    .single-author-title{
                        font-weight: $regular;
                        margin-bottom: 17px;
                        margin-top: 1px;
                        letter-spacing: 0;
                        @media only screen and (max-width: 767px){
                            margin-bottom: 15px;
                        }
                    }
                    p{
                        margin-bottom: 0;
                        font-size: 16px;
                        line-height: 1.75;
                    }
                }
            }
        }
        &.has-thumb{
            .entry-content-wrapper{
                margin-top: -200px;
                @media only screen and (min-width: 768px){
                    margin-top: -280px;
                }
            }
        }
    }
}